        <md-button
          class="md-simple md-just-icon md-round modal-default-button"
          @click="registrarModalHide"
        >x</md-button>
      </template>

      <template slot="body">
        <md-field>
          <label>Nome</label>
          <md-input v-model="name" type="text"></md-input>
        </md-field>
        <md-field>
          <label>E-Mail</label>
          <md-input v-model="email" type="email"></md-input>
