<script>
import { Modal } from "@/components";
import { log } from "util";
export default {
  components: {
    Modal
  },
  bodyClass: "landing-page",
  props: {
    header: {
      type: String,
      default: require("@/assets/img/bg7.jpg")
    },
    tip: {
      type: String,
      default: require("@/assets/img/tip.png")
    },
    teamImg1: {
      type: String,
      default: require("@/assets/img/faces/vicariato.jpg")
    },
    teamImg2: {
      type: String,
      default: require("@/assets/img/faces/olhar_rua.png")
    },
    teamImg3: {
      type: String,
      default: require("@/assets/img/faces/clovis2.jpg")
    }
  },
  data() {
    this.obterInfos()
    return {
      infos:this.obterInfos(),
      registrarModal: false,
      loginModal: false,
      name: null,
      email: null,
      cpf: null,
      senha: null,
      email_login: null,
      senha_login: null,
      confirmacaoSenha: null,
      message: null,
      institutions: null,
      image: require("@/assets/img/logos/logo.jpg"),
      endereco: {
        cep: "",
        logradouro: "",
        complemento: "",
        bairro: "",
        localidade: "",
        uf: "",
        number: ""
      },
      doacoes: {
        alimentacao: null,
        religiao: null,
        roupas: null
      }
    };
  },
  methods: {
    obterInfos() {
      this.$http.get("info").then(res => {
        
        this.infos = res.data;
      });
    },
    scrollToElement(element) {
      let element_id = document.getElementById(element);
      if (element_id) {
        element_id.scrollIntoView({ block: "start", behavior: "smooth" });
      }
    },
    busca_imagem(imagem) {
      console.log(imagem);
      return imagem.image;
    },
    registrarModalHide() {
      this.registrarModal = false;
    },
    loginModalHide() {
      this.loginModal = false;
    },
    salvar() {
      var institution = {
        name: this.name,
        email: this.email,
        cpf_cnpj: this.cpf,
        passwd: this.senha,
        address:
          this.endereco.logradouro +
          ", " +
          this.endereco.number +
          " - " +
          this.endereco.bairro +
          ", " +
          this.endereco.localidade +
          ", " +
          this.endereco.uf +
          " - " +
          this.endereco.cep,
        types: this.trataDoacoes(),
        shelter: 0
      };
      const metodo = "post";
      this.$http[metodo](`/institution`, institution).then(() => {
        this.flashMessage.show({
          status: "success",
          title: "Sucesso",
          message: this.$store.state.success.cadastro_instituicao
        });
      });
      this.registrarModalHide();
    },
    filtraInstituicao(value) {
      if (value.email == this.email_login) {
        return value;
      }
    },
    efetuaLogin() {
      this.$http
        .get(
          "institution/auth?email=" +
            this.email_login +
            "&passwd=" +
            this.senha_login
        )
        .then(res => {
          localStorage.token = res.data.token;
          var aux = null;
          this.$http.get("institution").then(res => {
            this.institutions = res.data;
            localStorage.logged_institution = this.institutions.filter(
              this.filtraInstituicao
            )[0].id;
            this.institutions = null;
            this.flashMessage.show({
              status: "success",
              title: "Sucesso",
              message: this.$store.state.success.generico
            });
            this.$router.push("events");
          });
        })
        .catch(err => {
          this.flashMessage.show({
            status: "error",
            title: "Error",
            message: this.$store.state.error.usuario_senha_invalida
          });
          this.$router.push("/");
        });
    },
    trataDoacoes() {
      var doacaoTratada = [];
      if (this.doacoes.alimentacao != null) {
        doacaoTratada.push(this.doacoes.alimentacao);
      }
      if (this.doacoes.roupas) {
        doacaoTratada.push(this.doacoes.roupas);
      }
      if (this.doacoes.religiao) {
        doacaoTratada.push(this.doacoes.religiao);
      }
      return doacaoTratada.join(",");
    },
    obterCep() {
      let campCep = document.getElementById("cep");
      if (this.endereco.cep.length == 8) {
        this.$correios.get(this.endereco.cep + "/json").then(res => {
          this.endereco = res.data;
        });
      }
    },
    criarEvento(){
      if(this.logged_institution){
        this.$router.push({name:"events"});
      }else{
        this.loginModal = true;
      }
    },
    recuperaToken() {
      if (this.logged_institution != null) {
        return this.$store.state.token;
      } else {
      }
    }
  },
  computed: {
    headerStyle() {
      return {
        backgroundImage: `url(${this.header})`
      };
    },
    logged_institution() {
      return this.$store.state.logged_institution;
    }
  }
};
</script>

<style lang="scss" scoped>
.md-card-actions.text-center {
  display: flex;
  justify-content: center !important;
}
.text-right {
  text-align: right;
}
.contact-form {
  margin-top: 30px;
}
.container-person {
  width: 80%;
}
.md-has-textarea + .md-layout {
  margin-top: 15px;
}

.img-tip {
  padding-left: 5px;
  max-width: 7%;
  max-height: 7%;
}
</style>
